.caja {
    display: flex;
    justify-content: center;
    /* background: red; */
    padding-bottom: 100px;
    margin-top: 145px;
}
.ventana-carrito {
    border-radius: 30px;
    text-align: center;
    /* border: 2px solid rgb(105, 103, 103); */
    background:#FDFAE8;
    cursor: default;
    /* height: 100%; */
    width: 90%;
    overflow: hidden;
}

.numero i {
    /* background: red; */
    /* border-radius: 10px; */
    font-size: 2rem;
    color:  #696969;
    width: 100%;
    /* background: red; */
}
.numero i:hover {
    /* background-color: black; */
    color: #00FFF3;
    cursor: pointer;
}
.item {
   
    display: flex;
    justify-content: center;
    column-gap: 25px;
    align-items: center; 
    /* background: green; */
    width: 100%;
    margin: 1rem auto;

}
.remove {
    font-size: 1rem;
    color:  #ff4b4b;
}
.item .numero {
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 20px;
    width: 10%;
}
.item .remove:hover {
    cursor: pointer;
}
h5 {
    /* background: pink; */
    width: 75%;
    text-align: left;
    font-size: 1.4rem;;
}

.item p{
    background: #FDFAE8;
    /* width: 50px; */
}
.ventana-carrito .comprar {
    text-decoration: none;
    display:inline-block;
    color: #FFFF;
    margin: 50px;

    padding: 10px 75px; /*Lo mismo pero da margen interno*/
    border-radius: 5px;
    background: rgba(0,0,0,1);
    transition: 0.2s;
}
.ventana-carrito .comprar:hover {
    background-color: #fff000;
    color: #000;
}
.linea {
    /* background: gray; */
    width: 90%;
}

