.banner{
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    column-gap: 50px;
    align-items: center;
    /* display: inline-block; */
    border: 2px solid rgb(105, 103, 103);
    background:#FDFAE8;
    box-shadow: 0 0.1rem 1.5rem rgba(0,0,0, 0.5);
    flex-grow: 1;
    flex-wrap: wrap;
    width: 290px;
    cursor: default;
    /* transition: 0.3s; */
    height: auto;
    max-width: 450px;  /*se recomienda imagen sin fondo*/
    /* min-width: 300px; */
    overflow: hidden;
}
.banner > img{
    height: 200px;
    width: auto;
    display: block; 
    background: yellow;
    /* padding: 0px auto; */
}
.descripcionRapida{
    text-align:justify; 
    padding: 10px;
    /* background: red; */
    margin-top: 0px;
    padding-top: 0px;
    /* height: auto; */
    line-height: 1.1; 

}
.descripcionRapida > h1{
    margin: 0;
    padding: 0;
    /* background: yellow; */
}
.descripcionRapida > .sub_descripcion> ul{
    list-style-type: none;
    margin: 0;
}
.btn{
    color: #fff;
    padding: 5px 50px;
    border-radius: 8px;
    background: rgba(0,0,0,1);
    font-size: 1.1rem;
    cursor: pointer;
    transition: 0.1s;
    text-decoration: none;

}
.btn:hover {
    background-color: #fff000;
    color: #000;
}