@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@300&display=swap');

.interface-carrito{
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    transition: all 0.3 ease-in-out;
    background: rgba(0,0,0, 0.5);
    z-index: 2;
}
