.ventana-modal {
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 2px solid rgb(105, 103, 103);
    background:#FDFAE8;
    /* cursor: default; */
    height: auto; /*600*/
    width: auto;

    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%) /*scale(0.8)*/;
    /* padding: 0px; */
    overflow: hidden;
    opacity:95%;
    /* visibility: hidden; */
    transition: all 0.3 ease-in-out;
    padding-bottom: 0px;
    padding-bottom: 10px;

}


