header {
    /* height: 28vh; */
    background-image: linear-gradient(rgba(0,0,0,0.3), rgba(0,0,0,0.4)),  /*Transparentar imagen*/
    url(../galeria/pollos_en_horno.jpg);
    background-repeat: no-repeat;
    background-size: cover; /*Cubrir toda la ventana*/
    /* background-attachment: fixed; */
    /* background-position: center; */
    position: fixed;
    top: 0px;
    width: 100%;
    /* background: red; */
  
  }
  .cabeza_1 {
    display: flex;
    justify-content: space-between;
    /* background: blue; */
    height: 95px;
    /* column-gap: 250px; */
    /* background: green; */
  }
  .titulo {
    display: flex;
    justify-content:center; /*Alinea a left, respecto a eje X*/
    align-items: center; /*Alinea los items en el centro respecto a eje Y*/
    padding-left: 10px; /*Da 10 px de separacion en la parte izquierda*/
    /* background: red; */
    /* column-gap: 10px; */
    /* background: red; */
    color: #FFFF;
    column-gap: 10px;
    text-decoration: none;
  }
  .vender {
    display: flex;
    align-items: center;
    align-items: center;
    color: #FFFB00;
    font-size: 2.5rem;
    margin: 0px;
    margin-right: 25px;
    width: 100px;
    column-gap: 20px;
    padding-right: 30px;
    /* background: red; */
    
  }
  .vender img {
    width: 3rem;
    /* background: red; */
    margin-top: 8px;
  }
  .carrito {
    position: relative;
    cursor: pointer;
  }
  .carrito Link {
    margin-top: 0.6rem;
    font-size: 3.5rem;
  }
  .carrito span {
    position: absolute;
    top: -0.5rem;
    right: -1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: red;
    color: yellow;
    padding: 0.5rem;
    padding-top: 0.1rem;
    padding-bottom: 0.1rem;
    font-size: 1.3rem;
    font-weight: 700;
    border-radius: 50%;
  }
  .nav {
    background-color: #f2f2f2;
    text-align: center;
    padding: 8px 0px;
  }
  nav a {
    text-decoration: none;
    display:inline-block;
    color: #696969;
    margin: 0px 12px; /*Da margen superior-inferior e izquierda-derecha*/
    padding: 5px 5px; /*Lo mismo pero da margen interno*/
    border-radius: 8px; /*Radio de los bordes*/
  }
  nav a:hover {
    background-color: black;
    color: white;
  }
